export default {
    data (){
        return {
            loading : false,
        }
    },
    methods :  {
        createPost(formData) {
            this.loading = true;
            this.$axios
                .post("https://jsonplaceholder.typicode.com/posts", {
                title: formData.title,
                body: formData.bady,
                userId: 1,
                })
                .then(() =>{
                this.loading = false;
                alert("Post Created")
                })
                
                .catch(function (error) {
                console.log(error);
                });
                
        }
    }
}
